import React, {useState, useEffect} from 'react'

const DisplayPlayer = ({ players, deletePlayer, whichPlayer, toggle}) => {
    const [remain, setRemain] = useState(0)


    const display = players.map(player => {
        return (
            <tr style={player.important ? { backgroundColor: 'orange' } : { backgroundColor: 'transparent' }}>
                <td>{player.id}</td>
                <td><span style={{ fontWeight: 'bolder' }}>{player.name}</span></td>
                <td>{player.club}</td>
                <td>
                    <button onClick={() => toggle(player.id, player)}>
                        {player.important ? 'Not important' : 'Important'}
                    </button>
                </td>
                <td>
                    <button onClick={() => whichPlayer(player)}>
                        Edit
                    </button>
                </td>
                <td>
                    <button onClick={() => deletePlayer(player.id)}>
                        Delete
                    </button>
                </td>
            </tr>
        )
    })

    useEffect(() => {
        setRemain(players.length)
    }, [players])

    return (
        <div>
            <h2>All players ({remain})</h2>
            <table>
                <tr>
                    <th>ID</th>
                    <th>NAME</th>
                    <th>CLUB</th>
                    <th>IMPORTANT</th>
                    <th>EDIT</th>
                    <th>DELETE</th>
                </tr>
                {display}
            </table>
        </div>
    )
}

export default DisplayPlayer