import React, { useState, useEffect } from 'react';
import AddPlayer from './components/AddPlayer'
import DisplayPlayer from './components/DisplayPlayers'
import EditPlayer from './components/EditPlayer'
import playerService from './services/players'

const App = () => {
  const [editing, setEditing] = useState(false)
  const [playerToEdit, setPlayerToEdit] = useState()
  const [players, setPlayers] = useState([])

  const addPlayer = obj => {
    const newObj = {
      name: obj.name,
      club: obj.club,
      important: false
    }

    playerService
      .create(newObj)
      .then(data => {
        setPlayers(players.concat(data))
      })
  }

  const deletePlayer = id => {
    const newArray = [...players]
    playerService
      .delPlayer(id)
      .then(data => {
        setPlayers(newArray.filter(el => el.id !== id))
      })
  }

  const whichPlayer = obj => {
    setEditing(true)
    setPlayerToEdit(obj)
  }

  const editPlayer = (id, obj) => {
    const newArr = [...players]
    playerService
      .update(id, obj)
      .then(data => {
        setPlayers(newArr.map(p => p.id === id ? data : p))
        setEditing(false)
      })
  }

  const toggle = (id, obj) => {
    const newArr = [...players]
    playerService
      .updateToggle(id, obj)
      .then(data => {
        setPlayers(newArr.map(p => p.id === id ? data : p))
      })
  }

  useEffect(() => {
    playerService
      .getAll()
      .then(data => {
        setPlayers(data)
      })
  }, [])

  return (
    <div className="center">
      <h1>Practicing</h1>
      <hr />

      {editing ?
        <EditPlayer
          setEditing={setEditing}
          playerToEdit={playerToEdit}
          editPlayer={editPlayer}
        /> :
        <AddPlayer
          addPlayer={addPlayer}
        />}

      <DisplayPlayer
        players={players}
        deletePlayer={deletePlayer}
        whichPlayer={whichPlayer}
        toggle={toggle}
      />
    </div>
  )
}

export default App